import { Controller, Inline, RangeSelection } from '@ali/4ever-cangjie';
import { Sticker } from '@ali/4ever-mo';
import { getRangesFromEditor }  from '@ali/4ever-utils';

/**
 * @description 获取选区内的 sticker 节点
 * @param {controller}
 * */
export default function getStickersAtRange(controller: Controller) {
  const { value } = controller;
  const ranges = getRangesFromEditor(controller);
  const inlines = ranges.reduce((pre: Array<Inline>, range: RangeSelection) => {
    const rangeInlines = value.document.getLeafInlinesAtRange(range);
    Array.prototype.push.apply(pre, rangeInlines);
    return pre;
  }, []);

  return inlines.filter((inline) => inline.type === Sticker.TYPE);
}
